import React from "react";
///ROUTER
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
///COMPONENTS
import Home from "./home/home";
import Todo from "./todo/todo";
import Cinema from "./cinema/cinema";
import Store from "./store/store";

function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path="/deploy-app" component={Home} exact />
          <Route path="/deploy-app/todo" component={Todo} exact />
          <Route path="/deploy-app/cinema" component={Cinema} exact />
          <Route path="/deploy-app/store" component={Store} exact />
          <Redirect to="/" />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
